$(".dropdown__btn").click(function() {
    $('.dropdown__menu').slideToggle();
  });
$(".search__btn").click(function() {
  $('.search__input').slideToggle(1);
});


$(".phone").click(function() {

  $('.popup').fadeIn();

	// Клик по ссылке "Закрыть".
	$('.popup-close').click(function() {
	  $(this).parents('.popup').fadeOut();
	});        

	// Закрытие по клавише Esc.
	$(document).keydown(function(e) {
		if (e.keyCode === 27) {
			e.stopPropagation();
			$('.popup').fadeOut();
		}
	});
	
	// // Клик по фону, но не по окну.
	// $('.popup-fade').click(function(e) {
	// 	if ($(e.target).closest('.popup').length == 0) {
	// 		$(this).fadeOut();					
	// 	}
	// });	
  $(document).mouseup( function (e) { 
                  var popup = $('.popup');
                  if (e.target != popup[0] && popup.has(e.target).length === 0){
                    $('.popup').fadeOut();
                  }
                });
});

$('.tabclick').click( function(e) {

                  e.preventDefault();
                  $('.tabclick').removeClass('active');
                  $(this).addClass('active'); 

                });
                $('.tabsone').click( function(e) {

                  e.preventDefault();
                  $('.tab-item').removeClass('tabs__active');
                  $('#tab1').addClass('tabs__active'); 

                });
                $('.tabstwo').click( function(e) {

                  e.preventDefault();
                  $('.tab-item').removeClass('tabs__active');
                  $('#tab2').addClass('tabs__active'); 

                });
                $('.tabsthree').click( function(e) {

                  e.preventDefault();
                  $('.tab-item').removeClass('tabs__active');
                  $('#tab3').addClass('tabs__active'); 

                });



$(".btn").on("click", function(){
  $(".btn").toggleClass("close-btn");
  $(".menu").toggleClass("open-menu");
});